/* Base styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
}

/* Exercise section styling - simplified without boxes */
.exercise-section {
  margin-bottom: 40px;
}

.exercise-section h2 {
  color: #444;
  margin-top: 0;
  margin-bottom: 15px;
}

/* Form Styling - only this gets a box */
.login-container {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.login-container h3 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  font-size: 20px;
  color: #333;
}

form {
  background-color: #f8f8f8;
  padding: 20px;
  border-radius: 5px;
  border: 1px solid #ddd;
}

.form-row {
  margin-bottom: 15px;
  display: flex;
  align-items: flex-start;
}

.form-row label {
  width: 150px;
  margin-right: 10px;
  text-align: right;
  font-weight: bold;
  font-size: 14px;
  margin-top: 7px;
}

.form-row input[type="text"],
.form-row input[type="password"],
.form-row select {
  flex: 1;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

.form-row select {
  height: 35px;
}

.label-with-text {
  margin-top: 0;
}

.radio-group,
.checkbox-group {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.radio-group div,
.checkbox-group div {
  display: flex;
  align-items: center;
}

.radio-group input,
.checkbox-group input {
  width: auto;
  margin-right: 5px;
}

.radio-group label,
.checkbox-group label {
  width: auto;
  margin: 0;
  text-align: left;
  font-weight: normal;
}

.form-buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
  margin-left: 160px; /* Aligns with the form inputs */
}

.form-buttons button {
  padding: 8px 20px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 14px;
}

.form-buttons button[type="reset"] {
  background-color: #f44336;
}

.form-buttons button:hover {
  opacity: 0.9;
}