/* styles.css */
.profile-page {
  font-family: Arial, sans-serif;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.profile-container {
  display: flex;
  margin-top: 20px;
}

.profile-image {
  margin-right: 20px;
}

.avatar {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
}

.user-details {
  flex: 1;
}

.user-info h2 {
  margin-top: 0;
  font-size: 1.8rem;
}

.user-posts ul {
  padding-left: 0;
  list-style: none;
}

.user-posts li {
  margin-bottom: 15px;
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 4px;
}

.user-posts h4 {
  margin-top: 0;
  margin-bottom: 5px;
  font-size: 1.2rem;
}

.user-posts h3 {
  font-size: 1.5rem;
  margin-top: 25px;
  margin-bottom: 15px;
}